export interface Vacation {
    id: string; // user id (email) - uuid
    title: string;
    description: string;
    type: VacationType; // e.g. cruise
}

export enum VacationType {
    cruise = 'cruise',
    travel = 'travel',
    sports = 'sports',
    shopping = 'shopping',
    flight = 'flight',
    business = 'business',
    virtual = 'virtual',
    nature = 'nature',
    other = 'other'
}

export class VacationUtil {
  public static capitalize(name?: string) {
    return name?.charAt(0)?.toUpperCase() + name?.slice(1)
  }
}
