import { Component } from '@angular/core';

import { AccountService } from './_services';
import { User } from './_models';

import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
    user: User;

    constructor(
      private matIconRegistry: MatIconRegistry,
      private domSanitizer: DomSanitizer,
      private accountService: AccountService
    ) {
      this.accountService.user.subscribe(x => this.user = x);
      this.matIconRegistry.addSvgIcon(
        'google',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/google.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'logout',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/logout.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'vh2',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/vh2.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'cruise',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/cruise.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'travel',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/travel.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'shopping',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/shopping.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'flight',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/flight.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'sports',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/sports.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'other',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/other.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'search',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/search.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'business',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/business.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'virtual',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/virtual.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'nature',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/nature.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'default-profile-image',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/default-profile-image.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'chevron-right',
        this.domSanitizer.bypassSecurityTrustResourceUrl('../assets/chevron-right.svg')
      );
    }

    logout() {
        this.accountService.logout();
    }
}
