import { Component, HostListener, ElementRef, ViewChild, AfterViewInit } from '@angular/core';

import { User } from '../_models';
import { AccountService } from '../_services';

import { HttpClient } from '@angular/common/http';

import * as THREE from 'three';

@Component({ templateUrl: 'home.component.html' })
export class HomeComponent implements AfterViewInit {
    user: User;
    fromRest: string;
    url = 'http://shira-jaxrs-docker-example-git-vachub.apps.us-west-1.starter.openshift-online.com/';
    // three js
    @ViewChild('rendererContainer') rendererContainer: ElementRef;
    renderer = new THREE.WebGLRenderer({antialias: true});
    scene = null;
    camera = null;
    mesh = null;

    constructor(private accountService: AccountService, private httpClient: HttpClient) {
        this.user = this.accountService.userValue;

        // three js
        this.scene = new THREE.Scene();

        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 10000);
        this.camera.position.z = 1000;

        const geometry = new THREE.BoxGeometry(200, 200, 200);
        const material = new THREE.MeshBasicMaterial({color: 0xB39DDB, wireframe: true});

        this.mesh = new THREE.Mesh(geometry, material);
        this.scene.add(this.mesh);
    }

    ngAfterViewInit() {
        // three js
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setClearColor('#e5e5e5');
        this.rendererContainer.nativeElement.appendChild(this.renderer.domElement);
        this.animate();
    }

    animate() {
        // three js
        window.requestAnimationFrame(() => this.animate());
        this.mesh.rotation.x += 0.01;
        this.mesh.rotation.y += 0.02;
        this.renderer.render(this.scene, this.camera);
    }

    // three js
    @HostListener('window:resize', ['$event'])
    onWindowResize(event) {
        this.renderer.setSize(event.target.innerWidth, event.target.innerHeight);
        this.camera.aspect = event.target.innerWidth / event.target.innerHeight;
        this.camera.updateProjectionMatrix();
    }
}
